
* {
    margin: 0;
    padding: 0;
}
body {
    background: #fff;
    font-family: 'Open-Sans',sans-serif;

}

#container{
  margin: 0 auto;
  position: relative;
  width:1200px;
  height:410px;
  overflow:hidden;
  justify-content: space-evenly;
  display: flex;
}


.svg {
  width:800px;
  height:300px;
  overflow: visible;
  position:absolute;
}

.grid .tick {
    stroke: lightgrey;
    opacity: 0.3;
    shape-rendering: crispEdges;
}
.grid path {
      stroke-width: 0;
}


#tag {
  color: rgb(37, 25, 25);
  background: #eecdd1;
  width: 150px;
  position: absolute;
  display: none;
  padding:3px 6px;
  margin-left: -80px;
  font-size: 11px;
}



#tag:before {
  border: solid transparent;
  content: ' ';
  height: 0;
  left: 50%;
  margin-left: -5px;
  position: absolute;
  width: 0;
  border-width: 10px;
  border-bottom-color: #eecdd1;
  top: -20px;
}

